import { useEffect } from 'react';

import PeerOpen from './PeerOpen';
import PeerConnect from './PeerConnect';
import PeerManager from './utils/PeerManager';
import ChatList from './ChatList';

export default function Chatting() {
    useEffect(() => {
        PeerManager.on('error', (error: Error) => {
            console.log(error);
        });
    }, []);
    return (
        <div className='relative p-4 h-screen'>
            <div className='flex h-16'>
                <div className='flex-1'>
                    <PeerOpen />
                </div>
                <div className='flex-1'>
                    <PeerConnect />
                </div>
            </div>
            <div className='h-chatListWrapper max-h-chatListWrapper'>
                <ChatList />
            </div>
        </div>
    );
}
