import { Fab } from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import { useEffect, useState } from 'react';
import PeerManager from './utils/PeerManager';

export default function ChatList() {
    const [text, setText] = useState('');
    const [list, setList] = useState<{ type: string; desc: string }[]>();

    const onInputChange = (e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        setText(e.target.value);
    };
    const onSendClick = () => {
        PeerManager.send({ type: '2', desc: text });

        setList((prev) => [...(prev ?? []), { type: '1', desc: text }]);
        setText('');
    };

    useEffect(() => {
        PeerManager.on('data', (data) => {
            setList((prev) => [...(prev ?? []), data]);
        });
    }, []);

    return (
        <>
            <div className='h-chatListContainer max-h-chatListContainer overflow-auto'>
                {list?.map((chat, i) => (
                    <div key={i} className={chat.type === '1' ? 'text-left' : 'text-right'}>
                        {chat.desc}
                    </div>
                ))}
            </div>
            <div className='flex items-center'>
                <input className='border rounded-xl h-10 flex-1' value={text} onChange={onInputChange} />
                <Fab size='small' onClick={onSendClick}>
                    <SendIcon className='ml-0.5' />
                </Fab>
            </div>
        </>
    );
}
