import { TextField, Button } from '@mui/material';
import { useEffect, useState } from 'react';
import PeerManager from './utils/PeerManager';

export default function PeerConnect() {
    const [id, setId] = useState('');
    const [open, setOpen] = useState('close');
    const [error, setError] = useState('');

    const onIdChange = (e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        setId(e.target.value);
    };
    const onConnectOpenClick = () => {
        PeerManager.connect(id);
    };

    useEffect(() => {
        PeerManager.on('connection:open', () => {
            setOpen('open');
        });
        PeerManager.on('connection:close', () => {
            setOpen('close');
        });
    }, []);

    return (
        <div>
            <TextField size='small' label='id' value={id} onChange={onIdChange} />
            <Button onClick={onConnectOpenClick}>Connect OPEN</Button>
            <div>OPEN : {open}</div>
            {error && <div>ERROR : {error}</div>}       
        </div>
    );
}
