import { fabric } from 'fabric';

export type TRendererConfig = {};

class Renderer {
    canvas?: fabric.Canvas;

    constructor() {}

    addCanvas(element: string | HTMLCanvasElement | null, options?: fabric.ICanvasOptions | undefined) {
        this.canvas = new fabric.Canvas(element, options);
        return this.canvas;
    }
    addUploadImage(file: File, imgOptions?: fabric.IImageOptions) {
        const url = URL.createObjectURL(file);
        fabric.Image.fromURL(url, (image) => this.canvas?.add(image), { ...imgOptions });
    }
    addTextbox(options?: fabric.ITextboxOptions) {
        const textbox = new fabric.Textbox('text', {...options});
        this.canvas?.add(textbox);
    }
}

export default new Renderer();
