import { IconButton } from '@mui/material';
import TextFieldsIcon from '@mui/icons-material/TextFields';
import Renderer from '../utils/Renderer';
import { Tooltip } from '../../AnimationTool.styles';

export default function InsertText() {
    const onInsertTextClick = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
        Renderer.addTextbox({ data: { type: 'textbox' }});
    };

    return (
        <Tooltip title='insert text' onClick={onInsertTextClick}>
            <IconButton>
                <TextFieldsIcon />
            </IconButton>
        </Tooltip>
    );
}
