import DefaultToolBox from './DefaultToolBox';
import OptionalToolbox from './OptionalToolbox';
import View from './View';

export default function AnimationTool() {
    return (
        <div>
            <div>
                <DefaultToolBox />
                <OptionalToolbox />
            </div>
            <div>
                <View />
            </div>
        </div>
    );
}
