import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { setCurrentTarget } from '../../../slices/animationTool';
import Renderer from '../utils/Renderer';

export default function View() {
    const dispatch = useDispatch();
    useEffect(() => {
        const canvas = Renderer.addCanvas('canvas');

        canvas.on('mouse:up', (e) => {
            const target = canvas.getActiveObject();
            const isActiveSelection = target?.type === 'activeSelection';

            if (!target) {
                dispatch(setCurrentTarget({ type: '' }));
                return;
            }

            if (isActiveSelection) {
                dispatch(setCurrentTarget({ type: 'group', object: target }));
                return;
            }

            dispatch(setCurrentTarget({ type: target.data.type, object: target }));
        });
    }, []);

    return (
        <div className='w-fit rounded-xl shadow m-auto'>
            <canvas id='canvas' width='800' height='450'></canvas>
        </div>
    );
}
