import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import Renderer from '../pages/AnimationTool/utils/Renderer';
import { RootState } from './index';

export type TState = {
    readonly currentTarget?: { type: string; object: fabric.Object };
};
const name = 'animationTool';

const initialState: TState = {};

const slice = createSlice({
    name,
    initialState,
    reducers: {
        setCurrentTarget: (state, action) => {
            state.currentTarget = action.payload;
        },
    },
});

export const { setCurrentTarget } = slice.actions;

export const selectAnimationTool = (state: RootState) => state[name];

export default slice.reducer;
