import { IconButton } from '@mui/material';
import InsertPhotoIcon from '@mui/icons-material/InsertPhoto';
import Renderer from '../utils/Renderer';
import { Tooltip } from '../../AnimationTool.styles';

export default function InsertImage() {
    const onInsertImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (!file) return;
        Renderer.addUploadImage(file, { data: { type: 'basic' } });
    };

    return (
        <Tooltip title='insert image' >
            <IconButton component='label'>
                <input hidden accept='image/png, image/jpg, image/jpeg' type='file' onChange={onInsertImageChange} />
                <InsertPhotoIcon />
            </IconButton>
        </Tooltip>
    );
}
