import { useNavigate } from 'react-router-dom';

export default function Main() {
    const navigate = useNavigate();

    const onChattingClick = ()=> {
        navigate('/chatting')
    }

    const onAnimationClick = () => {
        navigate('/animation-tool');
    };

    return (
        <div>
            <div className='border p-3 cursor-pointer text-center' onClick={onChattingClick}>CHATTING</div>
            <div className='border p-3 cursor-pointer text-center' onClick={onAnimationClick}>ANIMATION TOOL</div>
        </div>
    );
}
